<template>
    <v-container class="ma-0 pa-0" fluid>
        <v-row class="ma-0 pa-0">
            <v-col align="start" class="ma-0 pa-0">
                <v-img alt="" contain src="@/images/open-tracking-logo.png" style="width: 200px;margin-left: 50px;"/>
            </v-col>
        </v-row>
        <v-row class="ma-0 pa-0" style="background-color: #EFDFEE;">
            <v-col align="center">
                <v-row class="ma-0 pa-0" align="center" justify="center" style="height:500px">
                    <v-col>
                        <v-row class="ma-0 pa-0">
                            <v-col class="ma-0 pa-0" :style="isMobile ? 'font-size: 30px; color: #8D9CB9;' : 'font-size: 50px; color: #8D9CB9;'"><b>{{ trackingText1 }}</b></v-col>
                        </v-row>
                        <v-row class="ma-0 pa-0">
                            <v-col class="ma-0 pa-0" :style="isMobile ? 'font-size: 30px; color: #8D9CB9;' : 'font-size: 50px; color: #8D9CB9;'"><b>{{ trackingText2 }}</b></v-col>
                        </v-row>
                        <v-row class="ma-0 pa-0">
                            <v-col class="ma-0 pa-0" :style="isMobile ? 'font-size: 30px; color: #8D9CB9;' : 'font-size: 50px; color: #8D9CB9;'"><b>{{ trackingText3 }}</b></v-col>
                        </v-row>
                        <v-row class="ma-0 pa-0" align="center" justify="center">
                            <v-col cols="auto" align="center"><v-text-field hide-details label="GEM Master No" placeholder="Enter your tracking number" outlined @input="removeSpaces" @keyup="keyEnter" v-model="gemMasterNo" ref="gemMasterNo" dense style="width:400px;background-color:aliceblue"></v-text-field></v-col>
                            <v-col cols="auto" align="start"><v-btn class="white--text" color="#A9A4D5" @click="trackingSearch()"> Track Shipment </v-btn></v-col>
                        </v-row>
                        <v-row class="ma-0 pa-0">
                            <v-col class="ma-0 pa-0" style="font-size: 15px;color: #1815bb;text-decoration-line: underline;"><b :style="isMobile ? 'padding-right: 10px;cursor:pointer' : 'padding-right: 430px;cursor:pointer'" @click="goHome()">Go to GEM Platform</b></v-col>
                        </v-row>
                    </v-col>
                </v-row>
            </v-col>
            <v-col align="center">
                <v-img alt="" contain src="@/images/open-tracking-main.png" style="width: 500px;"/>
            </v-col>
        </v-row>
        <v-row class="ma-0 pa-0">
            <v-col class="ma-0 pa-0">
                <!-- 트래킹 -->
                <v-card v-show="this.shipmentDetail != ''">
                    <v-card-text class="ma-0 pa-0">
                        <v-container class="ma-0 pa-0" fluid>
                            <v-row>
                                <v-col align="center">
                                    <table style="border:0px;width:100%">
                                        <tr>
                                            <td colspan="5" style="background-color:#6587cd;width:100%;height:80px">
                                                <table style="font-color:red;width:100%">
                                                    <tr>
                                                        <td style="padding-left:10px"><font style="color:#ffffff">Your Parcel Status:</font></td>
                                                        <td rowspan="3" align="right"><v-img style="" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/tracking_top.png" width="230px" /></td>
                                                    </tr>
                                                    <tr>
                                                        <td style="height:50px;padding-left:10px"><font style="color:#ffffff"><h1> {{ shipmentDetail.trackingEventAbbreviation }}</h1></font></td>
                                                    </tr>
                                                    <tr>
                                                        <td style="padding-left:10px"><font style="color:#ffffff">Tracking #: {{ shipmentDetail.gemMasterNo }}</font></td>
                                                    </tr>
                                                </table>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <table style="padding-top:10px;padding-bottom:10px;width:100%">
                                                    <tr height="50px">
                                                        <td align="center" id="trackingStep1" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Label Created</font></td>
                                                        <td align="center" id="trackingStep2" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Shipped</font></td>
                                                        <td align="center" id="trackingStep3" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Clearance in<br>Progress</font></td>
                                                        <td align="center" id="trackingStep4" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Out for Delivery</font></td>
                                                        <td align="center" id="trackingStep5" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Delivered</font></td>
                                                    </tr>
                                                </table>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <table style="padding-left:10px;width:100%">
                                                    <tr>
                                                        <td style="width:140px"><font style="color:#be2b9b">Order #:</font></td>
                                                        <td v-if="shipmentDetail != ''">{{ shipmentDetail.orderNo }}</td>
                                                    </tr>
                                                    <tr>
                                                        <td><font style="color:#be2b9b">Sender:</font></td>
                                                        <td v-if="shipmentDetail != ''">{{ shipmentDetail.senderName }}</td>
                                                    </tr>
                                                    <tr>
                                                        <td><font style="color:#be2b9b">Shpping to:</font></td>
                                                        <td v-if="shipmentDetail != ''">{{ shipmentDetail.recipientName }}</td>
                                                    </tr>
                                                    <tr>
                                                        <td></td>
                                                        <td v-if="shipmentDetail != ''">{{ shipmentDetail.recipientPhoneNo }}</td>
                                                    </tr>
                                                    <tr>
                                                        <td></td>
                                                        <td v-if="shipmentDetail != ''">{{ shipmentDetail.recipientAddress1 }} {{ shipmentDetail.recipientAddress2 }}</td>
                                                    </tr>
                                                    <tr>
                                                        <td></td>
                                                        <td v-if="shipmentDetail != ''">{{ shipmentDetail.recipientAddressCity }},{{ shipmentDetail.recipientAddressState }},{{ shipmentDetail.recipientAddressCountryCode }}</td>
                                                    </tr>
                                                    <tr>
                                                        <td></td>
                                                        <td v-if="shipmentDetail != ''">{{ shipmentDetail.recipientZipCode }}</td>
                                                    </tr>
                                                    <!-- <tr>
                                                        <td><font style="color:#be2b9b">Item:</font></td>
                                                        <td v-if="shipmentDetail != ''">
                                                        {{ shipmentDetail.itemQuantity }} X {{ shipmentDetail.itemName }} (Qty: {{ shipmentDetail.itemTotalQuantity }}, Value: {{ shipmentDetail.itemTotalValueCurrency | currency }} {{ shipmentDetail.itemTotalValue | comma }})
                                                        </td>
                                                    </tr> -->
                                                    <tr>
                                                        <td><font style="color:#be2b9b">Carrier:</font></td>
                                                        <td v-if="shipmentDetail != '' && shipmentDetail.shippingInfo != null">{{ getCarrierName(shipmentDetail.shippingInfo.carrierCode) }}</td>
                                                    </tr>
                                                    <tr>
                                                        <td><font style="color:#be2b9b">Carrier Tracking #:</font></td>
                                                        <td v-if="shipmentDetail != '' && shipmentDetail.shippingInfo != null">
                                                            <button v-if="getTrackingUrl(shipmentDetail.shippingInfo.carrierCode, shipmentDetail.shippingInfo.trackingNo) != ''" class="linkButton" @click="openTrackingUrl(shipmentDetail.shippingInfo.carrierCode, shipmentDetail.shippingInfo.trackingNo)">{{ shipmentDetail.shippingInfo.trackingNo }}</button>
                                                            <button v-else>{{ shipmentDetail.shippingInfo.trackingNo }}</button>
                                                        </td>
                                                    </tr>
                                                </table>
                                            </td>
                                        </tr>             
                                            <tr style="height:10px">
                                                <td></td>
                                            </tr>
                                            <tr>
                                                <td>
                                                    <table style="padding:10px;width:100%;background-color:#f7c3c3;">
                                                        <tr>
                                                            <td colspan="3" style="padding-bottom:10px"><h2>Tracking History</h2></td>
                                                        </tr>
                                                        <tr>
                                                            <td colspan="3">
                                                                <div style="width: 100%; height:300px; overflow-y:auto; overflow-x: hidden;" id="trackingHistory">
                                                                <v-timeline dense clipped>
                                                                    <template v-for="(item, index) in this.shipmentDetail.trackings">
                                                                        <v-timeline-item v-if="index == 0" color="blue" icon-color="grey lighten-2" small :key="index">
                                                                            <v-row justify="space-between">
                                                                                <v-col cols="3">{{ item.trackingAt }}</v-col>
                                                                                <v-col>{{ item.eventDesc }}</v-col>
                                                                            </v-row>
                                                                        </v-timeline-item>
                                                                        <v-timeline-item v-else color="grey" icon-color="grey lighten-2" small :key="index">
                                                                            <v-row justify="space-between">
                                                                                <v-col cols="3">{{ item.trackingAt }}</v-col>
                                                                                <v-col>{{ item.eventDesc }}</v-col>
                                                                            </v-row>
                                                                        </v-timeline-item>
                                                                    </template>
                                                                </v-timeline>
                                                                </div>
                                                            </td>
                                                        </tr>
                                                    </table>
                                                </td>
                                            </tr>
                                    </table>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-card-text>
                </v-card>
                <!-- 트래킹 -->
            </v-col>
        </v-row>
        <v-row class="ma-0 pa-0">
            <v-col align="center">
                <v-img alt="" contain src="@/images/open-tracking-logo.png" style="width: 150px;"/>
                <b style="font-size: 18px;color: #8D9CB9;">Smart Logistics Solutions for Global Business</b>
            </v-col>
        </v-row>
        <v-row class="ma-0 pa-0">
            <v-col align="center" class="ma-0 pa-0">
                <span style="font-size: 15px;padding-bottom: 20px;">ⓒ 2023 GEM Platform. All rights reseved.</span>
            </v-col>
        </v-row>
        <!-- 로딩바 -->
        <loadingBarView :setLoadingBar="this.setLoadingBar"></loadingBarView>
        <!-- 로딩바 -->     
    </v-container>
</template>
<style scoped>
.linkButton{
  color: blue;
  text-decoration-line: underline;
}
</style>

<script>
import $ from "jquery";
import axios from "axios";
import common from '@/common'
import loadingBarView from "@/components/LoadingBarView.vue"
import commonCode from "@/common/Code"

export default {
    components: {
        loadingBarView
    },      
    data: () => ({
        trackingText1: "Complete one-stop",
        trackingText2: "logistics services",
        trackingText3: "with GEM Platform",
        setLoadingBar : { loadingBarDialogShow : false },
        gemMasterNo : "",
        shipmentDetail : "",
        trackingStepBackgroundColor: [],
    }),
    computed: {
      isMobile() {
        return this.$vuetify.breakpoint.width <= 480;
      }
    },
    methods: {
        scrollToBottom() {
          // 페이지의 맨 아래로 스크롤
          window.scrollTo({
              top: document.body.scrollHeight,
              behavior: 'smooth'
          });
        },
        removeSpaces() {
            this.gemMasterNo = this.gemMasterNo.replace(/\s/g, ''); // 공백을 제거하는 코드
        },
        keyEnter(event) { // event 인자 추가
            if (event.code == "Enter") {
                this.trackingSearch();
            }
        },
        trackingSearch() {
            if(this.gemMasterNo == "") {
                this.$alert(common.messages.openTrackingNumberCheck, '', 'info').then(() => { this.$refs.gemMasterNo.focus(); })
                return false;
            }
            this.setLoadingBar.loadingBarDialogShow = true;
            this.shipmentDetail = "";
            axios.get(common.makeAPIUrl('/prework/tracking?trackingNo=' + this.gemMasterNo)
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                console.log("response.data : "+JSON.stringify(response.data));
                this.shipmentDetail = response.data;
                this.shipmentDetail.trackingEventAbbreviation = commonCode.getTrackingEventAbbreviation(this.shipmentDetail.trackingEvent, this.shipmentDetail.recipientAddressCountryCode, this.shipmentDetail.recipientAddressCity);

                if(this.shipmentDetail.trackings != "") {
                    for(let index=0; index<this.shipmentDetail.trackings.length; index++) {
                        //이벤트 Desc 가져오기
                        this.shipmentDetail.trackings[index].eventDesc = commonCode.getTrackingEventDesc(this.shipmentDetail.trackings[index].event, this.shipmentDetail.trackings[index].city, this.shipmentDetail.trackings[index].countryCode);
                    }
                    this.trackingStepBackgroundColor = common.trackingStepBackgroundColor(this.shipmentDetail.trackings);
                }
                this.setLoadingBar.loadingBarDialogShow = false;

                setTimeout(() => {
                    this.scrollToBottom();
                }, 1000);
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => { 
                    this.setLoadingBar.loadingBarDialogShow = false;
                    this.$refs.gemMasterNo.focus();
                 })
            })
        },
        goHome() {
            window.open("https://www.gemplatforms.com/");
        },
        getCarrierName(value) {
          return common.getCarrierName(value);
        },
        getTrackingUrl(value, TN){
            return common.getTrackingUrl(value, TN);
        },
        openTrackingUrl(value, TN){
            return window.open(common.getTrackingUrl(value, TN),'_blank')
        },
    },
    mounted() {
        this.gemMasterNo =  this.$route.query.trackingNumber;
        if(this.gemMasterNo != "" && this.gemMasterNo != undefined) {
            this.trackingSearch();
            // 페이지가 모두 로드된 후 1초 뒤에 맨 아래로 스크롤
          setTimeout(() => {
              this.scrollToBottom();
          }, 1000);
        } else {
            this.$refs.gemMasterNo.focus();
        }
    },
    updated() {
        $("#trackingStep1").attr("style", this.trackingStepBackgroundColor.step1)
        $("#trackingStep2").attr("style", this.trackingStepBackgroundColor.step2)
        $("#trackingStep3").attr("style", this.trackingStepBackgroundColor.step3)
        $("#trackingStep4").attr("style", this.trackingStepBackgroundColor.step4)
        $("#trackingStep5").attr("style", this.trackingStepBackgroundColor.step5)

        $('#trackingHistory').scrollTop(0);
    }
}
</script>
